.work {
  background-color: blanchedalmond;
  border: 1px solid #000;
  cursor: pointer;
}

.workImage {
  width: 100%;
  max-height: 200px;
  overflow: hidden;
}

.workImage img {
  width: 100%;
}

.workInfo {
  font-size: 1rem;
}

.workTitle {
  font-size: 1rem;
  padding: 0.7rem 1rem;
}
.workDescription {
  font-size: 0.7rem;
  font-weight: 400;
  padding: 0rem 1rem;
}

.workLinks {
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-items: center;
  margin-top: 1rem;
}

.workLinks > li {
  display: flex;
  justify-content: center;
  background-color: red;
  align-items: center;
  align-self: center;
  padding: 1rem 0rem;
  flex: 1 1 30%;
  border-top: 1px solid black;
}
.workLinks > li:nth-child(2) {
  border-left: 1px solid black;
  border-right: 1px solid black;
}
.workLinks > li > a {
  text-decoration: none;
  color: #000;
  background-color: aquamarine;
}
